import "styles/global.css";
import "styles/fullpage.css";
import styled from "@emotion/styled";
import { useState, useRef, useEffect } from "react";
import { Camera } from "react-camera-pro";
import PictureProvider from "App";
import { Navigate, useNavigate } from "react-router-dom";
import character_1 from "assets/frame-character-1.png";
import character_2 from "assets/frame-character-2.png";
import character_3 from "assets/frame-character-3.png";
import character_4 from "assets/frame-character-4.png";
import { transform } from "typescript";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  width: 100vw;
  height: var(--vh);
`;

const Counter = styled.p`
  font-family: "Pretendard-ExtraBold";
  font-size: 20px;
  margin: 10px;
`;

const CameraLocation = styled.div`
  max-height: calc(100vh - 260px);
  display: flex;
  height: 100vmin;
  width: 100%;
  background-color: black;
  justify-content: center;
`;

const CameraView = styled.div<CameraProps>`
  position: relative;
  aspect-ratio: ${({ photoRatio }) => photoRatio};
  height: 100%;
  display: flex;
  overflow: hidden;
  background-color: gray;
`;

interface CameraProps {
  photoRatio: string;
}

const Button = styled.button`
  margin: 60px 0;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  box-shadow: 0 0 0 15px #000 inset;
  border: 10px;
`;

const Timer = styled.div`
  margin: 60px 0;
  width: 100vw;
  height: 100px;
  font-family: "Pretendard-ExtraBold";
  font-size: 50px;
  text-align: center;
  vertical-align: middle;
`;

const Character = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 9999;
`;

interface Props {
  readonly nextURL: string;
  readonly setPictures: ([]: string[]) => void;
  readonly isCharacter: boolean;
  readonly photoRatio: string;
}

const picArr: string[] = [];

const ShootingPage = ({
  nextURL,
  setPictures,
  isCharacter,
  photoRatio,
}: Props) => {
  const camera = useRef<any>(null);
  const [count, setCount] = useState(0);
  const [curCharacter, setCurCharacter] = useState<any>();

  const navigate = useNavigate();
  const GoToNextPage = () => {
    navigate(nextURL);
  };

  const buttonClick = () => {
    picArr.push(camera.current.takePhoto());
    setCount((x) => x + 1);
    if (count >= 3) {
      setPictures(picArr);
      GoToNextPage();
    }
    changeCharacter();
  };

  const changeCharacter = () => {
    if (count === 0) setCurCharacter(character_1);
    else if (count === 1) setCurCharacter(character_2);
    else if (count === 2) setCurCharacter(character_3);
    else if (count === 3) setCurCharacter(character_4);
  };
  useEffect(() => {
    changeCharacter();
  }, [count]);

  const [seconds, setSeconds] = useState<number>(10);
  const [visible, setVisible] = useState<boolean>(true);
  useEffect(() => {
    //  4  
    if (count < 4) {
      const interval = setInterval(() => {
        setSeconds((prev) => {
          if (prev <= 1) {
            setTimeout(() => {
              setVisible(true);
            }, 100); // 100ms = 0.1
            setVisible(false);
            buttonClick();
            return 10; //  10 
          }
          return prev - 1;
        });
      }, 1000);

      return () => clearInterval(interval); //     
    }
  }, [count]); // count   useEffect 

  return (
    <Container>
      <Counter>{count}/4</Counter>
      <CameraLocation style={{ display: visible ? "flex" : "none" }}>
        <CameraView photoRatio={photoRatio}>
          <Camera
            aspectRatio={"cover"}
            errorMessages={{
              noCameraAccessible:
                "     .     .",
              permissionDenied:
                "   .     .",
              switchCamera:
                "It is not possible to switch camera to different one because there is only one video device accessible.",
              canvas: "Canvas is not supported.",
            }}
            ref={camera}
          />
          {isCharacter && (
            <Character src={curCharacter} style={{ transform: "scaleX(-1)" }} />
          )}
        </CameraView>
      </CameraLocation>
      {/* <Button onClick={buttonClick}></Button> */}
      <Timer>{seconds}</Timer>
    </Container>
  );
};

export default ShootingPage;
